<template>
	<v-data-table
		title="Select a race to engage with"
		:headers="headers"
		:items="users2render"
		class="elevation-1"
		:loading="users2render === undefined"
		fixed-header
		dense
		no-data-text="No sellers yet"
	>
		<template slot="top">
			<v-toolbar-title style="padding: 20px; text-align: center">
				Sellers race ranking
			</v-toolbar-title>
		</template>
		<template slot="no-data">
			<v-alert type="error" outlined dense class="my-3"
				>No sellers yet</v-alert
			>
		</template>
		<template slot="item.name" slot-scope="{ item }">
			<div :class="item.me ? 'me' : ''">
				{{ item.name }}
			</div>
		</template>
	</v-data-table>
</template>

<script>
import {goal as WinScore} from '@/common/config' ;
export default {
	props: {
		sellers: Object,
		myId: String,
	},
	data() {
		return {
			snapshootUnsubscribe: null,
			headers: [
				{ text: "Seller name", value: "name" },
				{ text: "Proposals", value: "proposals" },
				{ text: "Score", value: "score" },
				{ text: "To win", value: "toWin" },
				{ text: "Position", value: "position" },
			],
		};
	},
	computed: {
		users2render() {
			return Object.entries(this.sellers)
				.map((s) => {
					return {
						...s[1],
						name: s[0],
						toWin: WinScore - s[1].score,
						me: this.myId && s[0] === this.myId,
					};
				})
				.sort((a, b) => b.score - a.score || b.proposals - a.proposals)
				.map((x, idx) => ({
					...x,
					position:
						idx === 0
							? "1st"
							: idx === 1
							? "2nd"
							: idx === 2
							? "3rd"
							: "" + (idx + 1) + "th",
				}));
		},
	},
};
</script>

<style>
.me {
	font-size: large;
	font-weight: bolder;
	color: green;
}
</style>
